<template>
  <div
    v-if="show"
    class="product-carousel__wrapper row recommended-products critical-component-clear"
  >
    <div class="product-carousel__content-wrapper">
      <div class="product-carousel__content">
        <div
          v-if="liquid.heading"
          class="product-carousel__title h4"
          v-text="liquid.heading"
        />

        <p
          class="product-carousel__subtitle h6"
          v-text="$string('product.recommended_products_subtitle')"
        />
      </div>
    </div>

    <div class="product-carousel__carousel-wrapper swiper-outer-container">
      <div
        ref="swiperPrev"
        class="swiper-button-prev swiper-button-custom"
        js-product-carousel="prev"
      >
        <icon-chevron-left />
      </div>

      <swiper
        :id="`ProductCarousel-${ liquid.sectionId }`"
        :modules="modules"
        :breakpoints="swiper.breakpoints"
        :prevent-interaction-on-transition="true"
        :navigation="swiper.navigation"
        :observer="true"
        :observe-parents="true"
        :slides-per-view="'auto'"
        :space-between="16"
        :threshold="5"
        class="product-carousel__carousel"
      >
        <template v-if="products.length">
          <swiper-slide
            v-for="(product) in products"
            :key="product.handle"
            class="product-carousel__slide"
            js-product-carousel="slide"
          >
            <product-card
              :product="product"
            />
          </swiper-slide>
        </template>

        <template v-else>
          <swiper-slide
            v-for="n in liquid.limit"
            :key="n"
            class="product-carousel__slide"
            js-product-carousel="slide"
          >
            <div class="product-carousel__placeholder" />
          </swiper-slide>
        </template>
      </swiper>

      <div
        ref="swiperNext"
        class="swiper-button-next swiper-button-custom"
        js-product-carousel="next"
      >
        <icon-chevron-right />
      </div>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Recommended products (recommended-products)
 * -----------------------------------------------------------------------------
 * Recommended Products Section used on PDP with Recommendation API.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
import { A11y, Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'

import ProductCard from '~async/product-card/product-card'
import IconChevronLeft from '~icons/direction/chevron/left.svg'
import IconChevronRight from '~icons/direction/chevron/right.svg'

import productRecommendations from '~/graphql/queries/productRecommendations.gql'
import { formatProductToLiquid } from '~/helpers/format'

export default {
  name: 'RecommendedProducts',

  components: {
    IconChevronRight,
    IconChevronLeft,
    ProductCard,
    Swiper,
    SwiperSlide,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        productId: '',
        sectionId: '',
        heading: '',
        limit: '',
      }),
    },
  },

  setup() {
    return {
      modules: [A11y, Navigation],
    }
  },

  data() {
    return {
      show: false,
      products: [],
      swiper: {
        breakpoints: {
          576: {
            spaceBetween: 16,
          },
          768: {
            spaceBetween: 24,
          },
        },
        navigation: {
          nextEl: '[js-product-carousel="next"]',
          prevEl: '[js-product-carousel="prev"]',
        },
        a11y: {
          prevSlideMessage: window.cnvs.strings.accessibility.carousel.previous,
          nextSlideMessage: window.cnvs.strings.accessibility.carousel.next,
        },
      },
    }
  },

  async mounted() {
    await this.getShopifyRecommendedProducts(this.liquid.productId)
    this.resetReviews()
  },

  methods: {
    async getShopifyRecommendedProducts(productId) {
       const id = `gid://shopify/Product/${productId}`

      try {
        const response = await cnvs.Query({
          locale: cnvs.store.language,
          query: productRecommendations,
          variables: {
            country: cnvs.store.country,
            id,
          },
        })


        if (!response.productRecommendations) {
          return
        }

        let limit = this.liquid.limit

        if (limit > response.productRecommendations.length) {
          limit = response.productRecommendations.length
        }

        for (let i = 0; i < limit; i++) {
          const formattedProduct = formatProductToLiquid(response.productRecommendations[i])
          this.products.push(formattedProduct)
        }

        this.show = true

      } catch(error) {
        console.error(error)
      }
    },

    resetReviews() {
      console.log('resetReviews', window.yotpoWidgetsContainer)
      if (window.yotpoWidgetsContainer) {
        window.yotpoWidgetsContainer?.initWidgets()
      }
    },
  },
}
</script>

<style lang="scss">
@import '../product-carousel/product-carousel';
</style>
